header{
    width: 1200px;
    height: 120px;
    margin: 0 auto;
    border-bottom:1px solid red ;
    position: relative;
}

header .logo{
    float: left;
    margin: 18px 10px 0 0;
}

 h1{
    text-indent: -999px;
}

header .logo a{
    display: block;
    width: 125px;
    height: 56px;
    background: url(http://css.gomein.net.cn/gmpro/1.0.0/public/1.0.0/css/i/logo.png) 0 0 no-repeat;
}


header .search{
    position: absolute;
    width: 500px;
    top: 32px;
    left: 385px;
    height: 52px;
 
}

header .search .searchtop{
    border: 1px solid #dd00a7;
    position: relative;
    font-size: 14px;
    height: 30px;
    background: #fff;
}

header .search .searchbottom{
    margin: 6px 0 0;
    color: #A5A5A5;
    line-height: 14px;
    height: 14px;
    font-size: 12px;
}
header .search .searchbottom a{
    display: inline-block;
    margin-right: 5px;
    line-height: 14px;
    color: #A5A5A5;
    font-family: 'microsoft yahei';
}

header .search .searchbottom a.highlight, header .search .searchbottom a:hover {
    color: #f5004b;
}

header .mycart{
    position: absolute;
    right: 0;
    top: 32px;
    width: 142px;
    height: 46.53px;
    background: red;
}